.attribution {
  font-size: 11px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 5px;
  font-weight: 300;
  margin: 30px 0;
}
.attribution a {
  color: hsl(238, 40%, 52%);
  text-decoration: none;
}

.attribution a:hover {
  text-decoration: underline;
  cursor: pointer;
}
