@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Rubik", sans-serif;
}

body {
  background-color: hsl(228, 33%, 97%);
  margin: 0 auto;
}
/* // BUTTON // */
button {
  background-color: hsl(238, 40%, 52%);
  color: hsl(0, 0%, 100%);
  width: 80px;
  /* text-align: center; */
  font-weight: 500;
  /* letter-spacing: 0.8px; */
  border: none;
  padding: 10px 0px;
  border-radius: 7px;
}
button:hover {
  cursor: pointer;
}

.attribution {
  font-size: 11px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 5px;
  gap: 5px;
  font-weight: 300;
  margin: 30px 0;
}
.attribution a {
  color: hsl(238, 40%, 52%);
  text-decoration: none;
}

.attribution a:hover {
  text-decoration: underline;
  cursor: pointer;
}

.comments-list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  width: 85%;
  max-width: 800px;
  font-size: 16px;
  z-index: 1;
}

.section-reply {
  margin-left: 25px;
  padding-left: 25px;
  border-left: 2px solid hsl(223, 19%, 93%);
  margin-bottom: 15px;
}

.comments-list .single-comment.reply-msg {
  width: 100%;
  /* padding-left: 60px;
  padding-top: 20px; */
}

@media (max-width: 460px) {
  .section-reply {
    margin-left: 10px;
    padding-left: 15px;
  }
}

.single-comment {
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-template-rows: auto auto auto;
  grid-row-gap: 8px;
  row-gap: 8px;
  grid-column-gap: 15px;
  -webkit-column-gap: 15px;
          column-gap: 15px;

  max-width: 800px;
  width: 100%;
  padding: 20px;
  border-radius: 7px;
  color: hsl(211, 10%, 45%);
  background-color: hsl(0, 0%, 100%);

  margin-bottom: 15px;
  line-height: 1.3em;
}

/* .comments-list .single-comment.reply-msg:last-child {
  margin-bottom: 0;
} */
/* GRID LAYOUT */
.likes-container {
  grid-row: 1 / -1;
  align-self: start;
}
.header-left {
  grid-row: 1 / 2;
  grid-column: 2 / 3;
}
.header-left img {
  width: 30px;
}
.comment-content {
  grid-row: 2 / 3;
  grid-column: 2 / -1;
}

.update-btn {
  justify-self: end;
  grid-column: 3 / -1;
}

/* OTHERS ELEMENTS */
/* ACTIVE USER LABEL IN HEADER */
.user-avatar {
  display: flex;
  align-items: center;
}
.active-user {
  background-color: hsl(238, 40%, 52%);
  color: hsl(0, 0%, 100%);
  margin: 0 5px;
  padding: 0px 5px;
  font-size: 0.8em;
  font-weight: 300;
}

/* HEADERS GENERAL */
.header-left,
.header-right {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
}
/* HEADERS RIGHT - REPLY AND EDIT */
.header-right,
.header-right.user-edit {
  width: -webkit-max-content;
  width: max-content;
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
  font-weight: 500;
}
.edit,
.delete {
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
}
.delete {
  margin-right: 5px;
  color: hsl(358, 79%, 66%);
}
.edit:hover,
.delete:hover,
.header-reply:hover {
  cursor: pointer;
}
/* CONTENT CONTAINER */

.reply-to {
  color: hsl(238, 40%, 52%);
  font-weight: 500;
}

/* GRID LAYOUT RESPONSIVE */
@media (max-width: 650px) {
  .single-comment {
    grid-template-columns: auto auto;
  }
  .single-comment .likes-container {
    grid-row: 3 / 4;
  }
  .single-comment .header-left {
    grid-row: 1 / 2;
    grid-column: 1 / -1;
  }
  .single-comment .header-right {
    grid-row: 3 / 4;
    grid-column: 2 / 3;
    justify-self: end;
  }
  .single-comment .comment-content {
    grid-row: 2 / 3;
    grid-column: 1 / -1;
  }
  .single-comment .update-btn {
    justify-self: end;
    grid-column: 2 / 3;
    grid-row: 5 / 6;
  }
  .single-comment.new-msg {
    align-items: center;
  }
  .single-comment.new-msg .user-avatar {
    grid-row: 2 / 3;
  }
  .single-comment.new-msg .send-btn {
    grid-row: 2 / 3;
    justify-self: end;
  }
  .single-comment.new-msg .new-content {
    grid-row: 1 / 2;
    grid-column: 1 / -1;
  }
}

.likes-container {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  justify-items: center;
  align-items: center;
  background-color: hsl(228, 33%, 97%);
  width: 36px;
  height: 80px;
  max-height: 120px;
  border-radius: 7px;
  font-size: 14px;
  font-weight: 500;
}
.score-minus,
.score-plus {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.likes-container .score-minus:hover,
.score-plus:hover {
  cursor: pointer;
}
@media (max-width: 650px) {
  .likes-container {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 35px;
    align-self: start;

    height: 36px;
    width: 85px;
  }
}

/* Content Editable */
.msg-editable {
  border-radius: 5px;
  padding: 5px;
  border: 1px solid hsl(223, 19%, 93%);
  min-height: 70px;
  width: 100%;
}
.msg-editable:focus {
  outline: 0px solid transparent;
}

.send-btn {
  align-self: start;
}

.modal-container {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  background-color: rgba(103, 114, 126, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  z-index: 2;
}

.modal-overlay {
  position: absolute;
  display: grid;
  grid-template-columns: 1fr;
  /* grid-template-rows: 1; */
  width: 85%;
  max-width: 350px;
  min-height: 220px;
  background-color: hsl(0, 0%, 100%);

  border-radius: 7px;
  padding: 25px;

  z-index: 100;
  opacity: 1;
}

.modal-overlay h3 {
  color: hsl(212, 24%, 26%);
  margin-bottom: 15px;
  font-size: 22px;
  align-items: center;
}

.modal-overlay p {
  color: hsl(211, 10%, 45%);
  font-size: 16px;
  line-height: 1.5rem;
  letter-spacing: 1px;
  margin-bottom: 15px;
}
.modal-overlay footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* gap: 8%; */
}
.modal-overlay button {
  padding: 13px 15px;
  min-width: 47%;
  font-size: 14px;
  font-weight: 600;
}

.btn-cancel {
  background-color: hsl(211, 10%, 45%);
  color: hsl(0, 0%, 100%);
}

.btn-delete {
  background-color: hsl(358, 79%, 66%);
  color: hsl(0, 0%, 100%);
}

.main-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

h1 {
  color: hsl(212, 24%, 26%);
  font-size: 28px;
  font-weight: 600;
  margin: 40px 0;
  text-align: center;
}

